.Navbar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 6vh;
}

.logo {
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: Roboto;
    height: 100%;
    display: flex;
    align-items: center;
}

.logo a {
    text-decoration: none;
    color: black;
}

.slider {
    width: 340px;
    margin: 0 10px;
    display: inline-block;
}

/*  
    slider in-build classes
    rc-slider-track
    rc-slider-handle
    rc-slider-rail
*/
.rc-slider-track {
    background-color: transparent;
}

.rc-slider-rail {
    height: 8px; 
}

.rc-slider-handle {
    background-color: green;
    outline: none;
    border: 2px solid green;
    box-shadow: none;
}